
.training__container {
 	background-color: white;
  	font-size: 16px;
 	font-weight: 300;
	border-bottom: 2px solid #f4f5f6;
  	padding: 10px 20px;
  	display : -ms-flexbox;
  	display : flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.training__school__description{
  margin: 10px 10px;
}

.training__school {
    padding: 5px 0px;
    margin: 10px 0px;
  	font-size: 1.5em;
   	font-weight: bold;
}
.training__school__name{
  font-weight: 300;
  font-size: 1.25em;
  padding-bottom: 15px;
}
.training_description_content{
  line-height: 1.5em;
}
.training__performance__list{
  padding: 5px 20px;
  margin: 10px 10px;
  list-style-type: circle;
  line-height: 1.5em;
  margin-bottom: 0.5em;
}
.training__performance__list li{
  margin-bottom: 0.5em;
}
@media screen and (max-width : 400px) {
  .training_container {
    width: 90%;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .training__school, .training__performance {
    -ms-flex-item-align: end;
        align-self: flex-end;
    font-size: 1em;
  }
  p, li{
    font-size: 14px;
    padding:0 0 10px 0;
    line-height: 1.5em;
  }
	.training__performance__list{
  		padding: 5px 0px;
	}
	.training__performance__list li{
  		margin-bottom: 0em;
	}
  
}




