
.experience__container {
    background: white;
    font-size: 16px;
    font-weight: 300;
    border-bottom: 2px solid #f4f5f6;
    padding: 10px 20px;
    margin-top: 10px;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.experience {
  font-size: 16px;
  font-weight: 300;
    border-bottom: 2px solid #f4f5f6;
  padding: 5px 0px;
}

.experience__company {
    padding: 5px 0px;
    margin: 10px 5px;
}

.experience__performance__list{
  padding: 5px 20px;
  margin: 10px 10px;
