    -ms-flex-direction: column;
        flex-direction: column;
  }
  .training__school, .training__performance {
    -ms-flex-item-align: end;
        align-self: flex-end;
    font-size: 1em;
  }
  p, li{
    font-size: 14px;
    padding:0 0 10px 0;
    line-height: 1.5em;
  }
	.training__performance__list{
  		padding: 5px 0px;
	}
	.training__performance__list li{
  		margin-bottom: 0em;
	}
  
}




