 	display : flex;
 	-ms-flex-direction: column;
 	    flex-direction: column;
 	moz-flex-direction: column;
 	-ms-flex-pack: center;
 	    justify-content: center;
}

@media screen and (max-width : 400px) {
	.container {
	 	display : -ms-flexbox;
	 	display : flex;
	 	-ms-flex-direction: column;
	 	    flex-direction: column;
	}
	
	
}




